import { Watchlist } from "@rp/common/api-types"
import { useClerkOrganizationMember } from "@rp/react/common/hooks"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { IconButton } from "@rp/react/web/mui/core"
import { useWatchlistsActions } from "../hooks/useWatchlistsActions"

type Props = {
  watchlist: Watchlist
}

export const WatchlistShareButton = ({ watchlist }: Props) => {
  const { data: orgUser } = useClerkOrganizationMember(watchlist.ownerUserId)
  const actions = useWatchlistsActions({
    organizationResource: orgUser,
  })
  return (
    !actions.share.allowedAction?.(watchlist) && (
      <RPTooltip
        disabledContent={actions.share.disabledAction?.(watchlist)}
        title={actions.share.label}
      >
        <IconButton
          disabled={actions.share.disabledAction?.(watchlist)}
          onClick={() => actions.share.onClick(watchlist)}
        >
          {typeof actions.share.icon === "function"
            ? actions.share.icon(watchlist)
            : actions.share.icon}
        </IconButton>
      </RPTooltip>
    )
  )
}
